.talk {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 36px 20px;

  .talk-search-wrapper {
    display: flex;
    align-items: center;
    gap: 4px;
    width: 100%;
    height: 60px;
    padding: 10px 10px 10px 20px;
    margin-bottom: 28px;
    border-radius: 51px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.2);
    overflow: hidden;
    .talk-search {
      flex: 1;
      height: 100%;
      &::placeholder{
        font-size: 14px;
      }
    }
    .talk-search-icon{
      height: 100%;
      object-fit: cover;
      cursor: pointer;
    }
  }

  .talk-info{
    display: flex;
    flex-direction: column;
    gap: 28px;
    width: 100%;
    padding: 20px 20px 32px;
    background-color: lightgray;
    border-radius: 30px;
    .talk-info-text{
      display: flex;
      flex-direction: column;
    }
  }

}
